<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.px-4.px-md-5.mb-5
    .BGIMG01.px-md-5.px-4.pt-md-5.pt-4
      .titulo-principal.color-acento-contenido
        .titulo-principal__numero
          span 3
        h1 Analítica de indicadores de desempeño en #[em social media]
      
      .row.justify-content-center.align-items-center.mb-5
        .col-lg-7.order-lg-1.order-2
          p La analítica es el proceso de cierre periódico que se realiza en la estrategia de #[em social media], relacionado con una marca. Esta actividad corresponde a la medición de resultados de la campaña de #[em social media], función que debe ejecutar el #[em community manager] para conocer la efectividad de las estrategias, midiendo los resultados, lo que se denomina analítica #[em web].
          .BGR01A.p-4(data-aos="fade-right")
            p.mb-0 Además, se debe llevar a cabo con las mejores herramientas que permitan analizar la dinámica de los usuarios y clientes, en los diferentes canales digitales aportará a un direccionamiento estratégico pertinente, de acuerdo con los objetivos propuestos y la dinámica del sector y así, poder obtener informes de #[em social media] que faciliten las evidencias, la efectividad de la gestión de #[em social media] y en especial, el cumplimiento de los objetivos. 
        .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
          img(src="@/assets/curso/tema3/img01.png")
      
      p Algunas de esas herramientas son: Google Analytics, Buzzsumo, Bit.ly, Facebook #[em Insights], Twitter Analytics, Metricool, Twitonomy, Followerwonk, Audiense, Hootsuite, Buffer, Sprout Social, Pirendo, TweetBinder, Iconosquare, INK 361.

      .mb-4.d-flex
        .d-flex.flex-wrap.align-items-center.pe-4
          img(src='@/assets/curso/temas/img01.svg' style="max-width: 32px;" data-aos="fade-right")
          h4.col.mb-0(style="margin-left: -16px" data-aos="fade-left") KPIS

    p Los #[em Key Performance Indicators] (KPI) o indicadores claves de rendimiento, están relacionados con la medición de resultados; por esta razón, se conocen como la analítica de #[em social media], porque es a partir de ella y los objetivos planteados en el plan de contenidos, que se deben definir, también se definen dependiendo el nivel de posicionamiento de la marca, y según la experiencia del #[em community manager] y el conocimiento de la marca. 
    p.mb-5 Sin embargo, se presentan a continuación, en las siguientes tipologías:

    .row.justify-content-center.mb-4
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src="@/assets/curso/tema3/img02.svg")
      .col-lg-8(data-aos="fade-left")
        LineaTiempoD.color-acento-botones
          p(numero="1" titulo="KPIS de contenidos") Miden aspectos como la cantidad de “me gustas” en un periodo de tiempo, la cantidad de veces que se comparte el contenido, comentario y clic, o ingresos a los contenidos. 
          p(numero="2" titulo="KPIS de alcance") Se refiere a la cobertura o cantidad de personas a quienes han llegado las publicaciones, cantidad de visitas en redes sociales, visitas ganadas, páginas vistas, nuevas visitas, porcentaje de rebote, tiempo promedio de duración de la visita y visitas totales a la página, desde las redes sociales. 
          p(numero="3" titulo="KPIS de interacción") El compromiso que se tiene en cada una de las redes sociales. 
          p(numero="4" titulo="KPIS de conversión") Porcentaje de visitas en redes sociales, cantidad de ventas producto de #[em social media] y el beneficio obtenido en redes sociales, de acuerdo con la inversión. 
          p(numero="5" titulo="KPIS de captación de <em>leads</em>") Los costos específicos de cada respuesta específica de clientes potenciales, a través de #[i e-mail]. en respuesta a los anuncios publicados en redes sociales. 
          p(numero="6" titulo="KPIS de fidelización") Mide la cantidad de visitas repetidas o concurrentes, y porcentaje de compra de usuarios anteriores. 

    p Se entiende que los KPIS de contenido, alcance, interacción, conversión, captación de #[em leads] y de fidelización, son grandes grupos que comprenden en su interior, una diversidad de indicadores KPIS con una función en específico, que permitirá brindar información a la organización.
    p.mb-5 De acuerdo a esto, se presentan algunos de los KPIS más representativos:

    .row.justify-content-center
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src="@/assets/curso/tema3/img03.svg")
      .col-lg-8(data-aos="fade-left")
        AcordionA.mb-5(tipo="a" clase-tarjeta="BGR01C")
          div(titulo="CTR")
            p.mb-0 El CTR (#[em Click Through Rate]), cuantifica la tasa de #[i clics] en enlaces publicados en redes sociales y el porcentaje de visitas logradas desde redes sociales, en comparación con las visitas totales.
          div(titulo="CPA")
            p.mb-0 El CPA (#[em Cost Per Acquisition]), costo de la campaña en redes sociales, en comparación con los compradores desde redes sociales.
          div(titulo="ROI")
            p.mb-0 El ROI (#[em Return of Investment]) es el beneficio logrado en redes sociales en comparación con el costo total invertido (publicidad ads, costo #[em community manager], etc).
          div(titulo="CPL")
            p.mb-0 El CPL (#[em Cost per Lead]) es el costo por cada #[i e-mail] conseguido, a través de anuncios por clientes potenciales.

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4
        img(src='@/assets/curso/temas/img01.svg' style="max-width: 32px;" data-aos="fade-right")
        h4.col.mb-0(style="margin-left: -16px" data-aos="fade-left") Plan de posicionamiento
    
    p El posicionamiento SEO también vinculado al #[em marketing] de contenido e #[em inbound marketing] que traduce #[em marketing] de atracción, es el basado en buscadores donde es el mismo usuario quien se acerca en búsqueda de los productos y servicios ofertados por la empresa. 
    
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.BGR01A.p-4.mb-4(data-aos="fade-right")
        p.mb-0 La diferencia que existe entre el #[b #[em outbound]] y el #[b #[em inbound]], es que el primero está enfocado en el producto, por lo tanto, llega sorpresivamente al usuario por diferentes medios y, el segundo, se encuentra centrado en el cliente, se encarga de publicar información estratégica del producto y servicio para que el cliente acceda a ella y luego realizar su adquisición o compra.

    p Este brinda toda una plataforma de soporte en cuanto a contenidos que se desarrolla en tres frentes:

    .BGIMG03.p-4.mb-5(data-aos="zoom-out")
      SlyderA(tipo="b")
        .row.justify-content-center.align-items-center
          .col-lg-7.order-lg-1.order-2
            .h4 #[em Marketing] de contenidos
            p Es una técnica de #[em marketing] que, mediante la creación y distribución de contenido relevante y de valor, para atraer al cliente potencial o interesado y convertirlo en cliente real y recurrente. Se centra en dar o entregar valor antes de pedirlo sin asediar al cliente con información y sin presionar por la compra, pero con el fin de hacerla efectiva haciendo uso de las herramientas de #[em marketing] de contenidos, gestiona información para ayudar o agregar valor a la necesidad de los clientes potenciales. 
          .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            img(src="@/assets/curso/tema3/img04.png")
        .row.justify-content-center.align-items-center
          .col-lg-7.order-lg-1.order-2
            .h4 SEO o posicionamiento #[em web]
            p Realiza un esfuerzo de posicionamiento de la información de la marca en los buscadores más reconocidos y utilizados con el fin que el cliente encuentre, con facilidad, la oferta de productos y servicios. Se trata de tener visibilidad en internet; mediante el uso de técnicas de SEO aportan a estar mejor ubicados en la red e incrementa la visibilidad. 
            p Es importante anotar que SEO traduce optimización para motores de búsqueda de tal manera que se optimice el uso de motores de búsqueda de manera que la posición de la marca esté en los primeros lugares para ser ubicados con facilidad cuando el usuario realice una búsqueda determinada relacionada con la actividad, productos y servicios de la marca. Y se desarrolla mediante las siguientes estrategias:
          .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            img(src="@/assets/curso/tema3/img05.png")
          .col.order-3
            p #[b Posicionamiento orgánico SEO]: es el posicionamiento natural por la configuración especial que se realiza de los motores y la marca al utilizar palabras claves; la optimización del #[em website] de la marca, al obtener enlaces externos y las conversaciones realizadas.
            p #[b Posicionamiento de pago SEM]: se apalanca en realizar publicidad para estar más visibles en los resultados de buscadores lo que logra a través de anuncios patrocinados.
            p #[b Posicionamiento de local SEO]: posicionar los establecimientos cercanos que cuentan con una dirección física y un lugar de atención.
            p.mb-0 En conclusión, los resultados orgánicos que lleva un tiempo conseguir es SEO y resultados de pago para llegar al primer tráfico por el camino más corto es SEM (Dotras, 2015c).
        .row.justify-content-center.align-items-center
          .col-lg-7.order-lg-1.order-2
            .h4 Acción en #[em social media]
            p Elegir los medios más atractivos para el cliente y de mayor conveniencia para la marca, con el fin de publicar contenidos y mantener conversaciones con el cliente. 
          .col-lg-5.col-10.order-lg-2.order-1.mb-lg-0.mb-4
            img(src="@/assets/curso/tema3/img06.png")

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4
        img(src='@/assets/curso/temas/img01.svg' style="max-width: 32px;" data-aos="fade-right")
        h4.col.mb-0(style="margin-left: -16px" data-aos="fade-left") Campañas de  #[em social media]
    
    p Los objetivos de la campaña se definen acordes con el plan estratégico de la organización, el nivel de reconocimiento de la marca en el sector y el mercado, y conforme a la estrategia de #[em marketing] digital elementos contundentes de planificación del rumbo de la organización hacia un posicionamiento estratégico en el mundo digital, conforme el grado de afinidad y relación, y el sector. En la siguiente tabla, se presentan algunos ejemplos de los objetivos de #[em social media].

    .row.justify-content-center.mb-4
      .col
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 3
          br
          span.fst-italic Fusión de objetivos generales con los de #[em social media marketing]

        .tabla-a.color-secundario(data-aos="zoom-in")
          table
            caption(style= "background-color: #FEF2E4") Nota. Sánchez-Guerrero, 2019. Recuperado de <a href="https://repositorio.comillas.edu/rest/bitstreams/295481/retrieve" target="_blank" style="color: #206897">repositorio.comillas.edu</a>
            thead(style="border-color: transparent")
              tr
                th(style="background-color: #ACE1E7;" rowspan="2").text-center Objetivos generales
                th(style="background-color: #ACE1E7;" colspan="3").text-center Las metas del #[em social media marketing]
              tr
                th(style="background-color: #FFF;").text-center Aprovechar nuevas posibilidades de desarrollo de negocio
                th(style="background-color: #FFF;").text-center Incrementar el tráfico #[em web] y las conversiones
                th(style="background-color: #FFF;").text-center Aumentar la presencia #[em online]
            tbody
              tr
                td(style="background-color: #F6F6F6;").text-center Aumentar las ventas y participación en el mercado.
                td(style="background-color: #F6F6F6;").text-center 
                td(style="background-color: #F6F6F6;").text-center Crear conciencia de los productos y servicios en existentes y nuevos usuarios.
                td(style="background-color: #F6F6F6;").text-center Nuevos contenidos que ofrezcan una experiencia diferencial.
              tr
                td(style="background-color: #FFF;").text-center Retener a los consumidores.
                td(style="background-color: #FFF;").text-center Proporcionar un servicio al cliente más personalizado.
                td(style="background-color: #FFF;").text-center Fortalecer las relaciones con los clientes, gracias a la directa interacción con ellos.
                td(style="background-color: #FFF;").text-center Obtener y considerar las recomendaciones y opiniones que los usuarios publican en redes sociales.
              tr
                td(style="background-color: #F6F6F6;").text-center Construir una base sólida de clientes.
                td(style="background-color: #F6F6F6;").text-center Incrementar el reconocimiento de la marca con nuevos canales.
                td(style="background-color: #F6F6F6;").text-center Crear consciencia de productos y servicios en nuevos clientes.
                td(style="background-color: #F6F6F6;").text-center 
              tr
                td(style="background-color: #FFF;").text-center Crear y reforzar una imagen de marca.
                td(style="background-color: #FFF;").text-center Incrementar la comunicación con usuarios, #[em partners], proveedores, etc. Transmitir un posicionamiento diferencial y de vanguardia.
                td(style="background-color: #FFF;").text-center
                td(style="background-color: #FFF;").text-center
              tr
                td(style="background-color: #F6F6F6;").text-center Incrementar la lealtad del consumidor.
                td(style="background-color: #F6F6F6;").text-center Proporcionar un servicio al cliente más personalizado.
                td(style="background-color: #F6F6F6;").text-center Fortalecer las relaciones con los clientes, gracias a la interacción con ellos.
                td(style="background-color: #F6F6F6;").text-center Obtener y considerar las recomendaciones y opiniones que los usuarios publican en redes sociales.

    p Las campañas en #[em social media], tienen su particularidad, de acuerdo con el canal o medio donde se desea hacer presencia, cada una requiere una planificación especial porque cada medio o red social tiene un enfoque específico, audiencia diferenciada, algunas pueden ser más interactivas y permiten hacer campañas visuales apoyadas en infografías, fotografías, videos, entre otros, pero existen otras más profesionales enfocadas en lo textual.
    p.mb-5 De esta manera, toda campaña debe considerar los siguientes requisitos:

    .row.justify-content-center.mb-4
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src="@/assets/curso/tema3/img07.svg")
      .col-lg-8(data-aos="fade-left")
        LineaTiempoD.color-acento-botones
          p(numero="1" titulo="Diagnóstico") Realiza un diagnóstico de la situación en línea.
          p(numero="2" titulo="Segmentación") Identificación y segmentación de la población objetivo.
          p(numero="3" titulo="Objetivos") Definir con claridad qué se espera lograr, y establecer los objetivos.
          p(numero="4" titulo="Canales") Elección de los canales, donde se proyecta tener presencia.
          p(numero="5" titulo="Estrategia de contenidos") Estrategia de contenidos, hacerlos amigables, dinámicos, interactivos y conversacionales.
          p(numero="6" titulo="Cronograma") Establecer un cronograma o calendario, del desarrollo de la estrategia.
          p(numero="7" titulo="Vinculación") Vinculación del #[em community manager] o demás profesionales requeridos.
          p(numero="8" titulo="Indicadores") Definir el sistema de medición, métricas, herramientas, kpis y el monitoreo.
          p(numero="9" titulo="Protocolo de comunicación") Diseñar protocolo de comunicación y crisis 2.0.
          p(numero="10" titulo="Presupuesto") Elaborar el presupuesto y definir con claridad la inversión a realizar.

    p Por otra parte, dentro de las campañas de #[em social media], debe existir una programación de contenidos que brindará información valiosa y, así mismo, dentro de este, incluir las diferentes estrategias de contenidos con el fin de cumplir con los objetivos planteados.
    p.mb-5 A continuación, se explican los puntos importantes a tener en cuenta, a la hora de crear la programación de contenidos:

    .row.justify-content-center
      .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
        img(src="@/assets/curso/tema3/img08.svg")
      .col-lg-8(data-aos="fade-left")
        AcordionA.mb-5(tipo="a" clase-tarjeta="BGR01C")
          div(titulo="Posicionamiento de marca")
            p.mb-0 El contenido en las redes, es parte esencial de la marca y busca posicionar la marca con una identidad propia, lo que se consolida como la estrategia de contenidos; debe ser cuidadosamente concebida de manera que defina la personalidad de la marca, al vincular los valores a transmitir, el tono a utilizar y el diseño creativo que la apoyará.
          div(titulo="Público objetivo")
            p.mb-0 Al identificar el público objetivo, se pueden definir las necesidades, temáticas específicas, la tipología de contenido, de acuerdo con cada uno de los canales en los que se tendrá presencia, lo cual permitirá identificar las diferentes audiencias lo que implica seleccionar diferente contenido a fin de satisfacer el tipo de audiencia, de acuerdo con la plataforma social; tal es el caso del contenido multimedia, tiene más concordancia con #[i Instagram] o #[i Facebook], y el contenido es más indicado para #[em blogs] o #[i Linkedln]. 
          div(titulo="Calendario editorial")
            p.mb-0 Para cumplir con este propósito, se requiere de un calendario editorial en el que debe incluirse el día y la hora de la publicación del contenido, la temática o contenido a publicar, el objetivo o fin a lograr, tipo de contenido a utilizar, si es video, texto, enlace, entre otros, la red social donde se va a publicar, otras particularidades como especificaciones, etiquetas y #[em hashtags], e indicadores para evaluar los resultados obtenidos. 

    p Finalmente, el lanzamiento de campañas de #[em social media], genera un trabajo exhaustivo en la dirección de #[em marketing], generar recursos adecuados, clasificarlos, analizar y publicar, sumado a responder diferentes solicitudes; por esta razón, es necesario calendarizar las publicaciones con el fin de ordenar los mensajes, la frecuencia y momento de la publicación en cada canal o redes sociales.
    
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-8.BGR01A.p-4.mb-4(data-aos="fade-right")
        p.mb-0 Por esta razón, en Internet, se encuentran disponibles, variedad de herramientas de automatización; sin embargo, aquí se mencionan algunas: #[i Buffer, Hootsuite, TweetDeck, Social Flow, SpreadFast], IFTTT, #[i Shedugram, Socialoomph, CrowdBooster] y #[i Viralwoot].

    .mb-4.d-flex
      .d-flex.flex-wrap.align-items-center.pe-4
        img(src='@/assets/curso/temas/img01.svg' style="max-width: 32px;" data-aos="fade-right")
        h4.col.mb-0(style="margin-left: -16px" data-aos="fade-left") Consolidación de informe de #[em social media] e inteligencia empresarial
    
    p En la consolidación de información, se evalúan las acciones realizadas en el sitio #[em web] y en redes sociales, como resultados de los reportes de la métrica, analítica y aplicación de herramientas de monitorización, resultados que deben ser analizados detalladamente y, al ser comparados con resultados obtenidos con antelación, y obtener nuevas conclusiones de la dinámica y tendencias de las campañas realizadas por la marca.
    p A continuación, se presenta la generación de un informe de #[em social media]:

    .row.justify-content-center
      .col-lg-8.order-lg-1.order-2(data-aos="fade-right")
        AcordionA.mb-5(tipo="a" clase-tarjeta="BGR01C")
          div(titulo="Soporte de la información")
            p.mb-0 Cuando se implementa el #[em marketing] digital, es necesario definir las herramientas que soporten la gestión de la información, de esta manera, uno de los productos que ellas arrojan son los reportes o informes específicos de la dinámica de las campañas difundidas en redes sociales.
          div(titulo="Importancia")
            p.mb-0 De esta manera, es importante anotar que estos están directamente relacionados con las métricas o analítica de #[em social media] la cual se debe medir periódicamente semanal, mensual o anualmente. Estos informes tienen real importancia porque aportan valor al desempeño del #[em community manager] o quien haga sus veces, evidencia la evolución de las cuentas de los clientes, muestra los resultados obtenidos en el logro de los objetivos propuestos, mide el retorno o beneficio obtenido y permite realizar ajustes o correcciones de ser necesarias.
          div(titulo="Aportes")
            p.mb-0 Los aportes de este tipo de reportes, permiten detallar, mejorar y profundizar en creación de contenido, programación más inteligente, estrategia dirigida, de acuerdo con el canal o red social, mantener el tono y lenguaje establecido y elegido y, el seguimiento y medición personalizados, que realiza.
          div(titulo="Contenido del reporte")
            p Por último, un reporte de #[em social media] debe contener: 
            ul.lista-ul--color
              li(style="margin-bottom: 2px")
                i.fab.fa-battle-net(style="color= #F40B74")
                | Fijación de objetivos.
              li(style="margin-bottom: 2px")
                i.fab.fa-battle-net(style="color= #F40B74")
                | Resultados del desempeño cuantitativo.
              li(style="margin-bottom: 2px")
                i.fab.fa-battle-net(style="color= #F40B74")
                | Resultados de desempeño cualitativo.
              li(style="margin-bottom: 2px")
                i.fab.fa-battle-net(style="color= #F40B74")
                | Herramienta usada e indicadores.
              li(style="margin-bottom: 2px")
                i.fab.fa-battle-net(style="color= #F40B74")
                | Análisis y sugerencias.
      .col-lg-4.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="fade-left")
        img(src="@/assets/curso/tema3/img09.svg")

    p A continuación, se presentan otros temas complementarios a tener en cuenta cuando se habla de #[em social media]:

    .BGIMG02.px-md-5.px-4.pb-md-5.pb-4
      .row.justify-content-center.mb-4
        .col-lg-4.col-8.mb-lg-0.mb-4(data-aos="fade-right")
          img(src="@/assets/curso/tema3/img10.svg")
        .col-lg-8(data-aos="fade-left")
          LineaTiempoD.color-acento-botones
            p(numero="1" titulo="Indicadores de rendimiento") Los indicadores de rendimiento, se establecen con el fin de calcular el beneficio o retribución que ha tenido la estrategia de #[em social media] para la marca. Además, de los indicadores antes mencionados, la estrategia de #[em social media] puede establecer sus propios indicadores de medición, de acuerdo con sus necesidades y objetivos.
            p(numero="2" titulo="Generalidades de minería de datos") Se encarga de extraer información de redes sociales, de acuerdo con las campañas que realiza la marca correspondiente a datos sociales, lo que requiere recopilar, procesar y analizar datos obtenidos de plataformas de redes sociales como #[i Facebook, Instagram, Twitter, TikTok, LinkedIn, YouTube], entre otras, para identificar patrones y tendencias preponderantes, como datos demográficos: edad, sexo, nivel de escolaridad, ocupación, ubicación geográfica, también intereses particulares tomados de los clic al consultar una página o al dar #[i clic] en “me gusta” en una publicación en especial, lo que permite generar conclusiones y proporcionar nuevos #[em insights]. 
            p(numero="3" titulo="Hallazgos") Los hallazgos en #[em social media] están orientados al cumplimiento o mejoramiento de la estrategia implementada, relacionados con los objetivos S.M.A.R.T. planteados en el plan de #[em marketing] y de contenidos, así como de la estabilidad de la estrategia planeada y ejecutada mediante campañas en diferentes canales, pero al final todos los hallazgos solo arrojan resultados que obligan a la marca a corregir errores o la mejora continua.
            p(numero="4" titulo="Interpretación y recomendaciones del análisis de indicadores") La interpretación y recomendación del análisis de indicadores, puede ser tan original como subjetiva, porque los resultados arrojados de la analítica son diferentes para cada marca, porque, aunque existen negocios similares las visiones de sus dirigentes y la proyección comercial que se desea tener no será exactamente la misma. De esta manera, aunque se participe en un mismo canal con productos y servicios iguales, la estrategia implementada siempre será diferente, esta dará cuenta de la proyección de la marca y las necesidades que tiene la organización.
            p(numero="5" titulo="Seguridad de la información") El avance tecnológico nos ha traído grandes ventajas y facilidades para la vida diaria, pero el estar conectados a red mundial se está expuesto a ser vulnerables por diferentes riesgos que se pueden sufrir si no se cuenta con la suficiente información o se cae en campañas prometedoras de premios o regalos, aparentemente fáciles de ganar. <br><br>Algunos riesgos en redes sociales son virus, #[em phishing] y robo de información, los cuales se pueden prevenir si se toman precauciones en cuanto a visitar sitios #[em web] que tengan un dominio real, a acceder a información acerca de promociones, ofertas de dinero fácil, donde se solicita información personal, entre otros.
            p(numero="6" titulo="Propiedad intelectual") Las redes sociales o #[em social media marketing], son una estrategia de comunicación en las organizaciones, basada en difundir contenidos en sus redes sociales, donde muestran la imagen, valores y contribuyen a la construcción de su marca.
              br
              br
              |Aunque parece un espacio de comunicación libre, es necesario tener en cuenta que existe la ley de protección intelectual donde se debe tener en cuenta que, siempre hay que contar con autorización del propietario de la creación, para hacer uso de contenido que no nos pertenece; es necesario revisar el aviso legal y, por último, la producción propia estará sometida a las mismas condiciones.


